@import url('https://fonts.googleapis.com/css2?family=Lobster&family=Roboto+Slab&display=swap');

body{
  background-color:#B3ECF2;
}

#containerHambMenu{
  width: 100%;
  height: 70px;
  display: flex;
  justify-content: end;
  align-items: center;
  align-content: center;
  color:azure;
  background-color: #365359;
  border: #365359 1px solid;
  font-family: 'Lobster', cursive;
  font-size:20px;
  border-bottom: 1px solid #E0DEDF;
}

#containerHambMenu img{
  display: none;
  width:8%;
  height:90%;
}

#containerHambMenu p{
  margin: 10px;
}

#buttonsContainer{
  background: linear-gradient(to bottom,#365359,#B3ECF2); 
  color:#365359;
  display: flex;
  justify-content: center;
  width: 207px;
  height:480px;
}

#buttonsContainer a{
  width:100%;
  height:120px;
  text-decoration: none;
}

#homeButton{
  width:100%;
  height:60%;
  color:#eee;
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  font-family: 'Roboto Slab', serif;
  font-size: 20px;
  transition: all 0.3s;
}

#homeButton:hover{
  background-color: #E0DEDF;
  color:#365359;
}

#curBudgetContainer{
  position: absolute;
  left: 17%;
  top:13%;
  display: flex;
  width: 825px;
  flex-direction: row;
  padding: 15px;
  justify-content: space-around;
  align-items: center;
  height: -moz-fit-content;
  height: fit-content;
  background-color: #B3ECF2;
  color: black;
  font-family: 'Roboto Slab', serif;
  font-size: 20px;
}

#addingButton{
  position: absolute;
  left: 99%;
  top: -20.2%;
  margin: 25px 0 20px 0;
  height: fit-content;
  width:3%;
  height: fit-content;
  color: #3B7302;
  font-family:'Roboto Slab', serif;
  font-size: 20px;
}

select{
  margin-right: 70px;
}

label{
  margin-right: 25px;
}

input{
  height:22px;
  margin-right: 70px;
  font-family:'Roboto Slab', serif;
  font-size: 17px;
  text-align: center;
}

.curBudgetChart{
  display:none
}

.chartShow{
  position: absolute;
  left:17%;
  top: 25%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0;
  width:70%;
  height:fit-content;
}

td{
  text-align:left;
  padding:0 0 0 15px;
}

.centeredTd{
  text-align: center;
  padding: 0;
}
   
@media only screen and (max-width: 600px) {  
            body{
              background-color:#B3ECF2;
            }

            #containerHambMenu{
              width: 96%;
              height: 15%;
              padding: 10px;
              display: flex;
              justify-content: space-between;
              align-items: center;
              align-content: center;
              color:azure;
              background-color: #365359;
              border-bottom: 5px solid #3B7302;
              font-family: 'Lobster', cursive;
              font-size:20px;
            }

            #containerHambMenu img{
              display: block;
              width:8%;
              height:90%;
            }
            
            #buttonsContainer{
              background: none;
              display: flex;
              padding:10% 0 10% 0;
              background-color: #365359;
              color: #365359;
              border: solid black 1px;
              width: 100%;
              height: -moz-fit-content;
              height: fit-content;
              margin-top: 17%;
              flex-direction: column;
              align-items: center;
            }

            #homeButton{
              width:40%;
              height:13%;
              background-color: #eee;
              color:#365359;
              border: solid white 1px;
              padding: 30px;
              display: flex;
              justify-content: center;
              align-items: center;
              box-shadow: 2px 2px 5px black;
              font-family: 'Roboto Slab', serif;
              font-size: 20px;
              transition: none;
            }

            #homeButton:hover{
              background-color: #eee;
            }

            a {
              width: 100%;
              margin:3% 0 3% 0;
              display: flex;
              justify-content:center;
              text-decoration: none;
            }

            #curBudgetContainer{
              position: static;
              display: flex;
              padding:15px;
              justify-content: center;
              margin:0;
              align-items: center;
              flex-direction: column;
              width:100%;
              height: fit-content;
              background-color: #B3ECF2;
              color:black;
              font-family:'Roboto Slab', serif;
              font-size: 20px;
            }

            .breakLine{
              display: block;
            }

            select{
              height:35px;
              font-family:'Roboto Slab', serif;
              font-size: 17px;
              margin:0;
            }

            input{
              height:35px;
              font-family:'Roboto Slab', serif;
              font-size: 17px;
              text-align: center;
              margin:0;
            }

            label{
              margin-right: 0;
            }
            
            #addingButton{
              position: static;
              margin: 25px 0 20px 0;
              height: fit-content;
              width:20%;
              color: #3B7302;
              font-family:'Roboto Slab', serif;
              font-size: 47px;
            }

            .curBudgetChart{
              display:none;
            }

            .chartShow{
              position: static;
              background-color: aliceblue;
              display: flex;
              flex-direction: column;
              justify-content: center;
              margin: 0;
              width:100%;
              height:fit-content;
            }

            td{
              text-align: center;
              padding:0;
            }
          } 